@import "~normalize.css";
@import "app/styles/vars";
@import "helper";

* {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}

body {
  font-family: " Pro W3", "Hiragino Kaku Gothic Pro", , Meiryo, "MSP", Arial, sans-serif;
  font-size: 16px;
  background: $grey-light;
  margin: 0;
  word-wrap: break-word;
  overflow: hidden;
}

.container {
  width: 1260px;
  height: 100%;
  @media screen and (max-width: 1260px) {
    width: 100%;
    max-width: 100%;
  }
}
.row {
  height: 100%;
  position: relative;
}

.col-md-8, .col-md-12, .col-md-4, .col-md-10 {
  height: 100%;
}

.abs-center {
  @include abs-center;
}

.d-flex {
  display: flex
}

h1 {
  font-size: 42px;
}

h2 {
  font-size: 36px;
}

h3 {
  font-size: 18px;
}

button, input, textarea {
  &:focus {
    outline: none;
  }
}

input.input-text {
  padding: 13px 15px;
  border: solid 1px $grey-dark;
  color: $black;
  min-width: 200px;
  border-radius: 4px;
  font-size: 14px;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.2);

  &[error=true] {
    border-color: $pink;
  }

  &[warning=true] {
    border-color: $orange;
  }

  &[disabled=true] {
    border-color: $grey;
    color: $grey;
  }

  &:focus {
    border-color: $primary;
  }

  &[display="block"] {
    display: block;
    width: 100%;
  }
}

.dropdown-input {
  position: relative;

  input {
    width: 100%;
  }

  &:before {
    right: 10px;
    content: "\f123";
    font-family: "Ionicons";
    font-size: 20px;
    @include abs-vertical;
  }
}

.joyride-tooltip__footer  {

  .joyride-tooltip__button {
    color: $white;
    font-size: 16px;
    border-radius: 4px;
    border: transparent;
    padding: 13px 24px;
    cursor: pointer;
    min-width: 128px;
    transition-duration: 0.4s;
    position: relative;
    box-shadow: 1px 1px $grey;

    &:hover, &:focus {
      color: $white;
      background-color: $btn-green-hover;
    }

    &.joyride-tooltip__button--primary {
      background-color: $btn-green;
    }

    &.joyride-tooltip__button--secondary {
      background-color: $grey;
      
      &:hover, &:focus {
        background-color: $grey-dark;
      }
    }
  }
}

.box_mail {
  background: $grey-light;
  padding: 10px 15px;
  border: 1px solid $grey;
  border-width: 1px 0;

  dl {
    width: 100%;
    margin: 0;
    @include clearfix;

    dt {
      width: 100px;
      float: left;
      span {
        float: right;
        padding-right: 10px;
      }
    }

    dd {
      width: calc(100% - 100px);
      float: left;
      margin: 0;
    }
  }
}

.mail_content {
  padding: 15px;
}
.joyride-tooltip {
  border-radius: 0;
  padding: 30px;
  &.bottom-left {
    margin-left: 14px;
    margin-top: 10px;
  }
  &.top-left {
    margin-left: 14px;
  }
  &.top {
    margin-bottom: 16px;
  }
  .bottom {
    margin-top: 6px;
  }
}
.joyride-tooltip__main {
  padding-right: 0;
  padding-bottom: 27px;
}
.joyride-hole {
  border-radius: 0;
}